
.noticeModal {
  position: absolute;
  top: 3rem;
  left: 0;
  width: 300px;
  height: 1.8rem !important;
  line-height: 0rem !important;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 1rem;
  overflow-x: hidden;
}
.noticeModal p {
  font-size: 0.6rem;
  position: relative;
}



